import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { SET_IO_IDX, SET_IO_TASKID, userContext } from '../../context/UserContext';
import Table, { TableBody, TableCell, TableHead, TableRow } from '../ui/table/Table';
import TextArea from '../ui/textarea/TextArea';
import styles from './EditIo.module.css';

import { TbCircleArrowRightFilled, TbCircleArrowLeftFilled } from 'react-icons/tb';

export default function EditIo() {
    const context = useContext(userContext);
    const [input, setInput] = useState('');
    const [output, setOutput]= useState('');

    // store 
    const taskId = context.state.data.io_taskId;
    const first_taskId = context.state.data.first_taskId;
    const last_taskId = context.state.data.last_taskId;
    const idx = context.state.data.io_idx;

    //  
    const [taskNum, setTaskNum] = useState(taskId);
    const [taskIdx, setIdx] = useState(1);

    // state 
    const handleChangeInput = (value) => {
        setInput(value);
    }
    const handleChangeOutput = (value) => {
        setOutput(value);
    }

    //   
    const handleLoad = async (e) => {
        axios.get(`/api/tasks/${taskNum}/assignment/${taskIdx}`)
        .then(function(res) {
            return res.data.data;
        })
        .then(function(data) {
            setInput(data.input);
            setOutput(data.output);
        })
        .catch(function(err) {
            if(err.response.status === 400){
                window.localStorage.removeItem("prompt-login");
                window.location.replace(window.location.href);
            }
        })
    }
    //   
    const handleSaveAndLoad = async (e) => {
        e.preventDefault();
        axios.patch(`/api/tasks/${taskNum}/assignment/${taskIdx}`, {
            input: `${input}`,
            output: `${output}`,
        })
        .then(function(res) {
            // console.log('result:');
            // console.log(res);
            if(taskIdx < 100){
                setInput('');
                setOutput('');
                //  index state 
                context.actions.contextDispatch({ type: SET_IO_IDX, data: (parseInt(taskIdx)+1)});
                // taskIdx 
                setIdx(prev => parseInt(prev) + 1);
            }
            else if(taskIdx >= 100){
                alert(' !');
            }
        })
        .catch(function(err) {
            console.log(err);
        })
    }
    // 
    const handleSave = async (e) => {
        e.preventDefault();
        axios.patch(`/api/tasks/${taskNum}/assignment/${taskIdx}`, {
            input: `${input}`,
            output: `${output}`,
        })
        .then(function(res) {
            // console.log(res);
            // return res;
        })
        .catch(function(err) {
            console.log(err);
        })
    }

    // task, index 
    const handlePressEnter = (e) => {
        const id = e.target.id;
        if(e.key === "Enter"){
            if(id === "task") {
                const value= e.target.value;
                value >= first_taskId && value <= last_taskId && setTaskNum(value);
                context.actions.contextDispatch({ type: SET_IO_TASKID, data: taskNum});
                context.actions.contextDispatch({ type: SET_IO_IDX, data: taskIdx});
                handleLoad(e);
            }
            else if(id === "idx") {
                const value= e.target.value;
                value >=1 && value <=100 && setIdx(value);
                context.actions.contextDispatch({ type: SET_IO_IDX, data: taskIdx});
                context.actions.contextDispatch({ type: SET_IO_TASKID, data: taskNum});
                handleLoad(e);
            }
        }
    }
    const handleOnBlur = (e) => {
        const id = e.target.id;
        if(id === "task") {
            const value= e.target.value;
            value >= first_taskId && value <= last_taskId && setTaskNum(value);
        }
        else if(id === "idx") {
            const value= e.target.value;
            value >= 1 && value <= 100 && setIdx(value);
        }
    }

    useEffect(() => {
        //    
        handleLoad();
    }, [taskId]);

    return (
        <div className={styles.ioEdit}>
            <div className={styles.header}>
                <p className={styles.title}>*       .</p>
                {/* taskId, idx input form */}
                <form
                    className={styles.ioForm}
                >
                    <label>task: </label>
                    <input 
                        type="number"
                        id="task"
                        onChange={(e) => {
                            const value = e.target.value;
                            value >= first_taskId && value <= last_taskId && setTaskNum(parseInt(e.target.value))
                        }}
                        max="120"
                        min="1"
                        value={taskNum}
                        onKeyDown={handlePressEnter}
                        onBlur={handleOnBlur}
                    />

                    <label>index: </label>
                    <input 
                        type="number"
                        id="idx"
                        onChange={(e) => {
                            const value = e.target.value;
                            value >=1 && value <=100 && setIdx(parseInt(e.target.value));
                        }}
                        max="100"
                        min="1"
                        value={taskIdx}
                        onKeyDown={handlePressEnter}
                        onBlur={handleOnBlur}
                    />
                    <span style={{
                        color: `#e02b2b`,
                        fontSize: `12px`,
                        marginLeft: `1em`,
                        lineHeight: `1.5em`,
                    }}>
                             .
                    </span>
                </form>
            </div>

            {/* io input form */}
            <form>
                <Table>
                    <TableBody>
                        <TableRow>
                            <TableHead>{`${idx}`}</TableHead>
                            <TableCell>
                                <TextArea input={input} setInput={handleChangeInput} placeholder={` .`}/>
                            </TableCell>
                        </TableRow>
                        <TableRow>
                            <TableHead>{`${idx}`}</TableHead>
                            <TableCell>
                                <TextArea input={output} setInput={handleChangeOutput} placeholder={` .`}/>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

                <div className={styles.buttons}>
                    <button
                        className={styles.moveBtn}
                        onClick={(e) => {
                            e.preventDefault();
                            if(taskIdx > 1){
                                context.actions.contextDispatch({ type: SET_IO_IDX, data: parseInt(taskIdx)-1});
                                setIdx(prev => parseInt(prev)-1);
                            }
                            else {
                                alert('.');
                            }
                        }}
                    ><TbCircleArrowLeftFilled/></button>
                    
                    <div className={styles.btnWrapper}>
                        <button onClick={handleSave} className={styles.button}></button>
                        <button onClick={handleSaveAndLoad} className={styles.button}>  </button>
                    </div>
                    
                    <button 
                        className={styles.moveBtn}
                        onClick={(e) => {
                            e.preventDefault();
                            if(taskIdx < 100){
                                context.actions.contextDispatch({ type: SET_IO_IDX, data: parseInt(taskIdx)+1});
                                setIdx(prev => parseInt(prev)+1);
                            }
                            else {
                                alert('.');
                            }
                        }}
                    ><TbCircleArrowRightFilled/></button>
                </div>
            </form>
        </div>
    );
}